@import url(https://fonts.googleapis.com/css?family=Play&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/***************************************************** PLAYER */
.player {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 4rem;
  width: 15rem;
  height: 23rem;
  box-shadow: 0 0 30px rgba(0,0,0,1);
  border-radius: 8px;
  border: 1px outset #1a1713;
  background-color: rgba(2, 128, 133, 0.8);
}

/***************************************************** Display */
.player__display {
  width: 95%;
  height: 60%;
  background-color: #000000;
  border: 1px outset #1a1713;
  margin-top: 0.2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  transition: all 0.3s linear;
}
.On {
  background-color: rgba(255, 255, 255, 0.979);
  box-shadow: 0 0 10px rgba(249, 249, 249, 0.2);
}
.playerLogo {
  display: none;
  margin-bottom: 1rem;
  width: 40%;
}
.On > .playerLogo, .On > .playerText {
  display: block;
}
.playerText {
  display: none;
  text-align: center;
}

/***************************************************** Buttons */
.btn__group {
  position: relative;
  height: 35%;
  width: 3rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}
.btn {
  position: absolute;
  border-radius: 100%;
  height: 3rem;
  width: 3rem;
  background-color: #fcfcfc;
  box-shadow: 0 0 4px rgb(0, 0, 0);
  outline: none;
  border: 1px solid #fcfcfc;
  font-size: 1.1em;
  color: #1c1c1c;
  cursor: pointer;
}
.plus {
  margin-top: -65%;
}
.minus {
  margin-top: 65%;
}
.play {
  margin-left: -130%;
}
.stop {
  margin-left: 130%;
}
.btn:hover {
  box-shadow: 0 0 13px rgba(252, 252, 252, 0.4);
  color: #121212;
}
.btn:active {
  box-shadow: 0 0 10px rgba(252, 252, 252, 0.2);
  border: 1px ridge #ffffff;
}

/***************************************************** Media queries */
@media only screen and (min-width : 320px) and (max-width : 768px) {
  .player {
    width: 60%;
    margin-top: 1rem;
    font-size: 0.8em;
  }
  .btn {
    width: 2.2rem;
    height: 2.2rem;
  }
  .plus {
    margin-top: -50%;
  }
  .minus {
    margin-top: 50%;
  }
}
@media only screen and (min-width : 568px) and (max-width : 825px) and (orientation : landscape) {
  .player {
    width: 12rem;
    height: 16rem;
  }
  .btn {
    height: 2.3rem;
    width: 2.3rem;
    font-size: 0.8em;
  }
  .plus {
    margin-top: -40%;
  }
  .minus {
    margin-top: 40%;
  }
}
@media only screen and (min-width : 768px) and (max-width : 1023px) and (orientation : portrait)  {
  .player {
    width: 15rem;
    height: 23rem;
  }
}
@media only screen and (min-width : 2000px) {
  .player {
    width: 20rem;
    height: 28rem;
  }
  .btn {
    height: 4rem;
    width: 4rem;
    font-size: 1.3em;
  }
  .plus {
    margin-top: -80%;
  }
  .minus {
    margin-top: 80%;
  }
  .play {
    margin-left: -180%;
  }
  .stop {
    margin-left: 180%;
  }
  .playerText {
    font-size: 1.4em;
  }
}

@media only screen and (max-height : 570px)  and (orientation : portrait) {
  .player {
    margin-top: 0;
    height: 14rem;
  }

  .playerLogo {
    width: 30%;
    margin: 0;
  }
  .player__display {
    height: 50%;
  }
}
@media only screen and (min-height : 570px) and (max-height : 700px)  and (orientation : portrait) {
    .player {
      margin-top: 0;
      height: 17rem;
    }
}

.skin__wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.skin__label {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 0.5rem;
  cursor: pointer;
  border-radius: 2px;
  padding: 0.1rem 0.8rem;
  background-color: rgba(237, 227, 225, 0.3);
  color: #08051b;
  font-size: 0.7em;
  font-weight: bold;
  transition: all 0.4s linear;
}
.skin__label:active {
  background-color: #b8ca13;
}
.skin__label > span {
    transition: all 0.2s linear;
    background: none;
    display:inline-block;
    width: 8px;
    height: 8px;
    margin-right: 6px;
}

.skin__label > input[type=radio]:checked + span {
    background: #b8ca13;
}

@media only screen and (max-height : 570px)  and (orientation : portrait) {
  .skin__label {
    font-size: 0.4em;
    margin: 0.5rem;
  }
  .skin__label > span {
    width: 4px;
    height: 4px;
  }
}
@media only screen and (min-width : 2000px) {
  .skin__label {
    font-size: 1.2em;
    margin: 1.5rem;
  }
}

/***************************************************** List of stations with search 'input' */
.list__station {
  width: 30%;
  text-align: center;
  height: 80%;
}
.list__station > ul {
  margin: 0;
  padding: 0;
  width: 100%;
  max-height: 70%;
  list-style: none;
  overflow: scroll;
  overflow-x: hidden;
  
}
::-webkit-scrollbar {
  width: 0px; 
}
.list__station_title {
  color: rgba(81, 89, 7, 0.5);
  font-size: 2em;
}

/***************************************************** Every station */
.station {
  width: 100%;
  height: 2.5rem;
  background-color: rgba(240, 223, 221, 0.3);
  color: #042529;
  font-weight: 600;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  transition: all 0.2s linear;
  padding: 0.2rem;
}
.station:hover {
   -webkit-transform: translateX(-3rem);
   transform:translateX(1rem);
   background-color: rgba(5, 43, 18, 0.2);
   color: #f5f0f0;
}
.logo {
  margin-left: 10%;
  max-width: 40px;
  max-height: 100%;
  margin-right: 10px;
}
.include {
  background-color: rgba(5, 43, 18, 0.2);
  color: #f5f0f0;
}
/***************************************************** Search 'input'  */
.dataSearch {
  width: 100%;
  height: 2.5rem;
  border: none;
  outline: none;
  padding: 0.2rem;
  text-align: center;
  background-color: #edeff0;
}

/***************************************************** Media queries  */
@media only screen and (min-width : 320px) and (max-width : 480px) {
  .list__station {
    width: 80%;
    height: 15rem;
  }
  .station {
    height: 1.8rem;
  }
}
@media only screen and (min-width : 568px) and (max-width : 825px) and (orientation : landscape) {
  .list__station {
    height: 100%;
  }
  .station {
    height: 1.3rem;
    font-weight: 400;
  }
}
@media only screen and (min-width : 2000px) {
  .station, .dataSearch {
    padding: 0.6rem;
    font-size: 1.5em;
  }
}

@media only screen and (max-height : 570px)  and (orientation : portrait) {
  .list__station {
    height: 15rem;
  }
  .list__station_title {
    margin-top: 0;
  }
  .station, .dataSearch{
    height: 1.2rem;
    font-size: 0.8em;
    font-weight: 400;
  }
}
@media only screen and (min-height : 570px) and (max-height : 700px)  and (orientation : portrait) {
  .list__station {
    height: 15rem;
  }
  .list__station_title {
    margin-top: 0;
  }
  .station, .dataSearch{
    height: 1.4rem;
    font-size: 0.8em;
    font-weight: 400;
  }
}
@media only screen and (min-height : 700px) and (max-height : 850px)  and (orientation : portrait) {
  .list__station {
    height: 23rem;
  }
}

